<!--  -->
<template>
  <div>
    <!-- {{this.n}} -->
     <!-- <button @click="tui">退出登录</button>  -->
    <header>
      <h1  class="left">
          <slot name="left"></slot>
      </h1>
    
         
    <h1 class="cen">
        {{con}}
    </h1>
   
        <h1  class="right"  v-if="!bool1">
          <div v-if="this.$store.state.obj.login==false"  class="ziyou">
            <router-link to="/login" tag="span">登陆</router-link>|<span>注册</span>
          </div>
          <router-link to="/my" tag="div" v-else class="imgyou">
        
      <svg viewBox="0 0 28 33" id="user" width="20%" height="20%"><path fill-rule="evenodd" d="M20.798 19.289c2.636-2.002 4.215-5.091 4.215-8.437 0-5.886-4.845-10.647-10.808-10.647S3.397 4.966 3.397 10.852c0 3.345 1.578 6.433 4.212 8.435l.264-2.678C4.358 18.32 1.591 21.403.168 25.187l1.478.556v-1.579c-1.485.73-1.485.73-1.501 1.079-.054.188-.054.188-.069.278a2.58 2.58 0 0 0-.026.229 9.112 9.112 0 0 0-.019.4c-.008.265-.014.617-.018 1.039-.005.511-.006 1.037-.006 1.451v.027c-.004 1.732 1.41 3.129 3.154 3.129h22.082a3.18 3.18 0 0 0 3.172-3.153l.011-1.305.008-.897.003-.296.001-.083v-.022-.006-.001l.002-.278-.093-.262c-1.385-3.918-4.203-7.122-7.812-8.88l.263 2.678zm-1.911-2.516l-2.045 1.553 2.309 1.125c2.856 1.392 5.106 3.949 6.218 7.093l-.09-.54V26.033l-.001.083-.003.296-.008.897-.011 1.305c0 .01-.011.021-.013.021H3.161c-.007 0 .005.011.005.032v-.031c0-.404.001-.92.006-1.418.004-.4.01-.732.017-.969.004-.121.008-.212.012-.262l-.006.043c-.009.06-.009.06-.058.229-.01.336-.01.336-1.49 1.063H2.74l.385-1.024c1.141-3.035 3.35-5.495 6.131-6.849l2.309-1.124-2.045-1.554c-1.859-1.412-2.964-3.576-2.964-5.92 0-4.129 3.418-7.488 7.649-7.488s7.649 3.359 7.649 7.488c0 2.344-1.106 4.509-2.966 5.921z" class="path1"></path></svg>

    </router-link>
           
        </h1>
         <h1 class="right"  v-else-if="bool1" @click="add($event)">{{main}}</h1>
     </header>
  </div>
</template>

<script>

export default {
  data () {
    return {
    };
  },
props:["goback","con","data","bool1","main"],
  components: {},

  computed: {
  },

  mounted(){
   
  },

  created(){},

  methods: {
      add(e){
        console.log(e.target.innerHTML)
        if(e.target.innerHTML=="切换城市"){
          this.$router.push("/home")
        }else if(e.target.innerHTML=="完成"||e.target.innerHTML=="编辑"){
          this.$emit("bianji")
        }
      }
},
}

</script>
<style scoped lang="scss">
header{
  z-index:3;
  position: fixed;
  top: 0;
  width: 100%;
h1{
  width:32%;
  text-align: right;
  font-size: 0.3rem;
line-height: 1rem;
&.left{
  margin-left: 0.1rem;
}
&.right{
  text-align: right;
   .ziyou{
      text-align: right;
      margin-left: 0.4rem;
    
   }
   .imgyou{
margin-left:1.5rem
   }
}
&.cen{
  text-align: center;
}
}
  display: flex;
  z-index: 5;
  flex-wrap: wrap;
      color: #fff;
    height: 1rem;
  background: #3190e8;
  font-size: 2rem;
  width: 100%;

}

.right>div{
  display: flex;
  width: 100%;
}

.imgyou{
  svg,path{
    margin-top:0.3rem;
  font-size: 0.3rem;
  color: white!important;
  border: white;
  fill: #fff;
}
}
</style>


